@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
* {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
body {
  overflow-x: hidden;
}
.App {
  height: 100vh;
  width: 100vw;
}

.banner__title {
  color: white;
  font-size: 2rem;
  letter-spacing: 3.5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  background-attachment: fixed;
  background-position: center;
  background-attachment: scroll;
  background-size: cover;
  background-repeat: no-repeat;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.7)),
    url(/extremetransportation/static/media/banner.e7d1445a.jpg) center no-repeat;
}

.banner__title > h1 {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  margin-bottom: 2%;
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
}
.banner__title > p {
  font-size: 1.2rem;
}

.banner__title > button {
  color: #39393a;
  height: 65px;
  width: 200px;
  margin-top: 7%;
  font-size: 22px;
  cursor: pointer;
}

/* Subheadline */

.sub__headline-container {
  width: 100%;
  height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: whitesmoke;
}

.sub__headline-container > p {
  color: #39393a;
  font-size: 1.2rem;
  margin-top: 2%;
}

.sub__headline-box {
  margin-top: 7%;
  height: 10vh;
  width: 20%;
  font-size: 1.5rem;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(0, 45, 114, 0.9);
  border: 1px solid #39393a;
  cursor: pointer;
  transition: ease-in-out 1s;
  z-index: 10;
}
.sub__headline-box:hover {
  width: 100%;
  font-size: 2.5rem;
  height: 15vh;
  border-top: 4px solid #39393a;
  border-bottom: 4px solid #39393a;
}
/* Scrolled Contact Button */

.scrolled__contact-button {
  height: 3em;
  width: 6.5em;
  font-weight: 600;
  cursor: pointer;
  color: #002d72;
  font-size: 1.4rem;
  background-color: rgb(255, 255, 255, 0.7);
  border: 2px solid black;
  position: fixed;
  left: 3%;
  top: 30%;
  z-index: 10000;
}

.animated__headline-container {
  display: flex;
  justify-content: center;
  width: 40%;
  margin: 5px;
  font-size: 2rem;
  color: #39393a;
}

