/* Section 4 */

.section__four {
  height: 70%;
  display: grid;
  align-items: center;
  justify-content: space-around;
  background: whitesmoke;
}

.section__four > p {
  color: #002d72;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.4rem;
  font-weight: 600;
}

.section__four-title {
  width: 100%;
  text-align: center;
}

.section-four-content {
  width: 100%;
  text-align: center;
}
.section__four-img {
  display: flex;
  justify-content: center;
}

.section__four-img > img {
  width: 350px;
}

/* media quieries */
@media only screen and (max-width: 1400px) {
  .section__three-title,
  .section__one-title {
    display: grid;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .section__three-title > p,
  .section__one-title > p {
    width: 100%;
    font-size: 32px;
  }
  .section__three {
    display: grid;
  }
}
/* gallery */

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  max-width: 100rem;
  margin: 0 auto;
  padding: 0 2rem 2rem;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  /* Compensate for excess margin on outer gallery flex items */
  margin: -1rem -1rem;
}

.gallery-item {
  /* Minimum width of 24rem and grow to fit available space */
  flex: 1 0 24rem;
  /* Margin value should be half of grid-gap value as margins on flex items don't collapse */
  margin: 1rem;
  box-shadow: 0.3rem 0.4rem 0.4rem rgba(0, 0, 0, 0.4);
  overflow: hidden;
}

.gallery-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 400ms ease-out;
}

.gallery-image:hover {
  transform: scale(1.15);
}

/*

The following rule will only run if your browser supports CSS grid.

Remove or comment-out the code block below to see how the browser will fall-back to flexbox styling. 

*/

@supports (display: grid) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(24rem, 1fr));
    grid-gap: 2rem;
  }

  .gallery,
  .gallery-item {
    margin: 0;
  }
}

@media only screen and (max-width: 1200px) {
  .section__two > img {
    display: none;
  }
  .section__two-title {
    width: 100%;
    text-align: center;
  }
  .section__two-title > p {
    font-size: 32px;
  }
  .section__two-title > h1 {
    font-size: 52px;
  }
}

@media only screen and (max-width: 900px) {
  .sub__headline-box {
    width: 100%;
    background-color: rgb(216, 216, 216);
    color: #002d72;
  }
}

@media only screen and (max-width: 700px) {
  .section__one-text {
    line-height: 1.5rem;
  }
  .section__two-text {
    font-size: 1.3rem !important;
    line-height: 1.6rem;
  }
}

@media only screen and (max-width: 550px) {
  .section__two-icons-container {
    grid-template-columns: repeat(1, 1fr);
  }
  .section__two-text {
    width: 95%;
    height: 100%;
  }
  .section__two-icon-container {
    width: 100%;
    height: 100%;
  }

  .section__two-icon-container {
    flex-direction: row;
  }
}
@media only screen and (max-width: 500px) {
  .section__four-img > img {
    width: 250px;
  }
}
@media only screen and (max-width: 430px) {
  .section__four-img > img {
    width: 180px;
  }

  .section__one {
    overflow-y: scroll;
  }
}

.nav {
  height: 90px;
  width: 100%;
  display: flex;
  align-items: center;
  z-index: 100;
  background: #ffffff;
  /* position: absolute;
  top: -100%; */
  color: #002d72;
  font-weight: 600;
  box-shadow: 0px 12px 15px 3px rgba(0, 45, 114, 0.3);
  /* animation: nav 0.5s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards; */
}

.link-tag {
  text-decoration: none !important;
  display: flex;
  align-items: center;
}

.application-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.logo {
  width: 12em;
}
