  z-index: 10000000;
}

.logo__contact {
  width: 90%;
  border-radius: 25px 25px 0 0;
}
.contact__address {
  color: #39393a;
}

.contact__phone > h1 {
  font-size: 2.5rem;
}
.contact__phone-desc {
  color: #39393a;
  margin: 6% 0;
}

.contact__phone > a {
  text-decoration: none;
  color: #002d72;
  text-align: center;
}
.contact__phone:hover {
  border-bottom: 2px solid #002d72;
}

.application {
  padding: 10px;
  text-align: center;
  font-size: 1.6rem;
  color: #002d72;
  margin-top: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}
@media only screen and (max-width: 750px) {
  .contact__phone > h1,
  .contact__address {
    font-size: 1.3rem;
  }
}
@media only screen and (max-width: 700px) {
  .contact {
    width: 100vw;
  }
}

.footer {
  height: 50%;
  width: 100%;
  background-color: rgb(216, 216, 216);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.footer__icons {
  color: #000000;

  width: 33.25%;
  padding: 15px;
  display: flex;
  border-bottom: 2px solid #000000;
  justify-content: space-evenly;
  cursor: pointer;
  -webkit-animation: shine 2s infinite;
          animation: shine 2s infinite;
}
.facebook-icon {
  -webkit-animation: shine1 2s infinite;
          animation: shine1 2s infinite;
}
.instagram-icon {
  -webkit-animation: shine2 2s infinite;
          animation: shine2 2s infinite;
}
.twitter-icon {
  -webkit-animation: shine3 2s infinite;
          animation: shine3 2s infinite;
}

.linkedin-icon {
  -webkit-animation: shine4 2s infinite;
          animation: shine4 2s infinite;
}

@-webkit-keyframes shine1 {
  0% {
    color: #000000;
  }
  25% {
    color: #002d72;
  }
  100% {
    color: #000000;
  }
}

@keyframes shine1 {
  0% {
    color: #000000;
  }
  25% {
    color: #002d72;
  }
  100% {
    color: #000000;
  }
}
@-webkit-keyframes shine2 {
  0% {
    color: #000000;
  }
  50% {
    color: #002d72;
  }
  100% {
    color: #000000;
  }
}
@keyframes shine2 {
  0% {
    color: #000000;
  }
  50% {
    color: #002d72;
  }
  100% {
    color: #000000;
  }
}
@-webkit-keyframes shine3 {
  0% {
    color: #000000;
  }
  75% {
    color: #002d72;
  }
  100% {
    color: #000000;
  }
}
@keyframes shine3 {
  0% {
    color: #000000;
  }
  75% {
    color: #002d72;
  }
  100% {
    color: #000000;
  }
}
@-webkit-keyframes shine4 {
  0% {
    color: #000000;
  }

  100% {
    color: #002d72;
  }
}
@keyframes shine4 {
  0% {
    color: #000000;
  }

  100% {
    color: #002d72;
  }
}

.facebook-icon:hover {
  color: #002d72;
}
.instagram-icon:hover {
  color: #002d72;
}
.twitter-icon:hover {
  color: #002d72;
}
.linkedin-icon:hover {
  color: #002d72;
}

.footer__info {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
}

.footer__contact,
.footer__personal {
  text-align: center;
  display: flex;
  flex-direction: column;
  width: 20%;
  height: 10em;
  font-size: 18px;
}
.footer__contact > h5,
.footer__personal > h5 {
  font-size: 22px;
  color: #002d72;
}

.footer__personal > ul > li,
.footer__contact > ul > li {
  list-style: none;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  color: #000000;
}
.footer__personal > ul,
.footer__contact > ul {
  display: grid;
  height: 100%;
  align-items: center;
}

@media only screen and (max-width: 720px) {
  .footer {
    height: 600px;
  }

  .footer__info {
    display: grid;
  }
  .footer__contact,
  .footer__personal {
    width: 100%;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  max-width: 100rem;
  margin: auto;
  padding: 5rem;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  /* Compensate for excess margin on outer gallery flex items */
  margin: -1rem -1rem;
}

.gallery-item {
  /* Minimum width of 24rem and grow to fit available space */
  flex: 1 0 24rem;
  /* Margin value should be half of grid-gap value as margins on flex items don't collapse */
  margin: 1rem;
  box-shadow: 0.3rem 0.4rem 0.4rem rgba(0, 0, 0, 0.4);
  overflow: hidden;
}

.gallery-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 400ms ease-out;
}

.gallery-image:hover {
  transform: scale(1.15);
}

/*

The following rule will only run if your browser supports CSS grid.

Remove or comment-out the code block below to see how the browser will fall-back to flexbox styling. 

*/

@supports (display: grid) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(24rem, 1fr));
    grid-gap: 2rem;
  }

  .gallery,
  .gallery-item {
    margin: 0;
  }
}

@media only screen and (max-width: 530px) {
  .container {
    padding: 0;
  }
}

