.circle-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 50%;
}

.circle {
  height: 100%;
  border: 3px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1.4rem;
  padding: 20px;
  border-radius: 100px;
  -webkit-animation: border 3s infinite;
          animation: border 3s infinite;
}
@-webkit-keyframes border {
  0% {
    border: 3px solid black;
    color: #002d72;
  }
  100% {
    border: 3px solid #002d72;
    color: black;
  }
}
@keyframes border {
  0% {
    border: 3px solid black;
    color: #002d72;
  }
  100% {
    border: 3px solid #002d72;
    color: black;
  }
}

.circle-icon {
  font-size: 1.7em !important;
}

.mission-text {
  font-size: 1.6rem;
  text-align: center;
  padding: 0 10%;
}
